import React, { Component } from 'react';
import pc from "./pc.scss";
import Hammer from "react-hammerjs";
import {Button,Tag} from 'antd'; 
class Pcs extends Component {
  constructor(props){
      super(props)
     
      this.state={
        // musicURL:props.musicURL,
        // musicAuthor:props.musicAuthor,
        // id:props.id,
        // musicPIC:props.musicPIC,
        ispc:true
      }
  }  
  componentWillUnmount() {
   
    // window.removeEventListener("resize", this.handleResize.bind(this));
  }
  componentDidMount() {
    window.addEventListener("resize", this.handleResize.bind(this)); //监听窗口大小改变
 
  }
  componentWillMount(){
    var e = window.innerWidth;
   
    if (e <= 1150) {
      this.setState({
          ispc:false
        }) 
   } else {
     this.setState({
       ispc:true
     }) 
   } 
   
    
   
  }

  download=()=>{
    var this_=this
    window.open(this_.props.musicURL)
  }
  handleResize = e => {
    // this.setState({
    //   innerWidth:e.target.innerWidth 
    // })
    if (e.target.innerWidth <= 1150) {
       this.setState({
           ispc:false
         }) 
    } else {
      this.setState({
        ispc:true
      }) 
    } 
    console.log("浏览器窗口大小改变事件", e.target.innerWidth);
  };
  render() {
    return (
       <div className={pc.pcbox} key={this.props.id}>
           <div className={pc.img}>
              <img src={this.props.musicPIC} alt=""/>
           </div>
           <div className={pc.text}>
               <div className={pc.title}>
                  
                 {this.props.musicTitle}
               </div>
               <div className={pc.info}>
                 {this.props.musicAuthor}
               </div>
           </div>
           <div className={pc.btnBox}>
           <span   style={{ display:(this.state.ispc?"inline-block":"none") }}> 曲号：  <Tag color="#87d068"> {this.props.id}</Tag> /</span>  

           <span  style={{ display:(this.state.ispc?"inline-block":"none") }}> 类型：  <Tag color="orange"> {this.props.tagsName}</Tag> /</span>  
             
             <Hammer onTap={()=>{this.props.changeMusic({

                     musicTitle: this.props.musicTitle,
                     musicAuthor: this.props.musicAuthor,
                     tagsName: this.props.tagsName,
                     musicPIC: this.props.musicPIC,
                     musicURL: this.props.musicURL
             })}}>
                 <span>
                   <Button type="primary" size="small"  style={{marginLeft:'10px'} }>播放</Button>
                   </span>  
             </Hammer>
             
               
           </div>
           
           
       </div>
    );
  }
}

export default Pcs; 


