import React, { Component } from "react";

import MainScss from "./Main.scss";
import { Button, Row, Col, Layout, Icon, Menu, Drawer } from "antd";
import { Link, Route, Redirect } from "react-router-dom";
 import Hammer from "react-hammerjs";
import Zanmei from "./zanmei/zanmei";
import Huigai from "./huigai/huigai";
import Lvdong from "./lvdong/lvdong";

const { Header, Content, Footer } = Layout;
class Main extends Component {
  constructor() {
    super();
    this.state = {
      autoRouter: ["/Main/Zanmei"],
      ispc: true,
      visible: false
    };
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize.bind(this));
  }

  componentWillMount() {
    var e = window.innerWidth;

    if (e <= 1150) {
      this.setState({
        ispc: false
      });
    } else {
      this.setState({
        ispc: true
      });
    }
  }
  showDrawer = () => {
    this.setState({
      visible: true
    });
  };

  onClose = () => {
    this.setState({
      visible: false
    });
  };

  routers = url => {
    // var router=  browserHistory.getCurrentLocation().pathname;
        //  正确从赞美跳转到律动
    if(url != this.props.location.pathname&&this.props.location.pathname=="/Main/Zanmei"){
      var audio = document.getElementById("audio");
      if(audio){
        if (audio.readyState != 4) {
           return false;
         } else {
          this.props.history.push(url);
         }
      }
       

    }
       //  正确从律动跳转到赞美
       console.log()
    if (url != this.props.location.pathname&&this.props.location.pathname=="/Main/Lvdong") {
          this.props.history.push(url);
    } 
   

    //
  };
  handleResize = e => {
    // this.setState({
    //   innerWidth:e.target.innerWidth
    // })
    if (e.target.innerWidth <= 1150) {
      this.setState({
        ispc: false
      });
    } else {
      this.setState({
        ispc: true
      });
    }
    console.log("浏览器窗口大小改变事件", e.target.innerWidth);
  };
  render() {
    return (
      <div className={MainScss.Main}>
        <Layout className="layout" style={{ height: "100%" }}>
          <Header
            style={{
              backgroundColor: "#000",
              overflow: "hidden",
              display: this.state.ispc ? "block" : "none"
            }}
            className="ssss"
          >
            <div className={MainScss.logo}>
              <img src={require("../logo.png")} style={{ width: "100%" }} />
            </div>
            <Menu
              theme="dark"
              mode="horizontal"
              // defaultSelectedKeys={
              //   this.props.location.pathname == "/Main/Zanmei" ||
              //   this.props.location.pathname == "/Main" ||
              //   this.props.location.pathname == ""
              //     ? ["/Main/Zanmei"]
              //     : [this.props.location.pathname]
              // }
              style={{ lineHeight: "64px", backgroundColor: "#000" }}
              onSelect={({ item, key, selectedKeys }) => {
                this.routers(  key );
              }}
            >
              <Menu.Item key="/Main/Zanmei" style={{backgroundColor:(this.props.location.pathname=="/Main/Zanmei"?"orange":"#000")}}>
                {/* <Link to="/Main/Zanmei" replace> */}
                  赞美
                {/* </Link> */}
              </Menu.Item>
              <Menu.Item key="/Main/Lvdong" style={{backgroundColor:(this.props.location.pathname=="/Main/lvdong"?"orange":"#000")}}>
                {/* <Link to="/Main/Lvdong" replace> */}
                  律动
                {/* </Link> */}
              </Menu.Item>
            </Menu>
          </Header>
          <div
            className={MainScss.IphoneHeader}
            style={{ display: !this.state.ispc ? "block" : "none" }}
          >
            赞美与律动
            <div className={MainScss.IphoneMenu}>
              {/* <Hammer onTap={this.showDrawer}>
               <span> */}
              <Icon
                type="bars"
                onClick={this.showDrawer}
                style={{ fontSize: 25 }}
              />
              {/* </span> */}
              {/* </Hammer> */}
            </div>
          </div>
          <Content style={{ padding: "0  ", height: "100%" }}>
            <div style={{ background: "#fff", padding: 0, height: "100%" }}>
              <Route
                exact
                path="/Main"
                render={() => <Redirect to="/Main/Zanmei" />}
              />
              <Route path="/Main/Zanmei" component={Zanmei} />
              <Route path="/Main/Lvdong" component={Lvdong} />
            </div>
          </Content>
        </Layout>
        <Drawer
          title="赞美与律动"
          placement="right"
          closable={false}
          onClose={this.onClose}
          visible={this.state.visible}
        >
          <p>
            <Hammer
              onTap={() => {
                this.routers("/Main/Zanmei");
              }}
            >
              <span>
                <Button
                  type="primary"
                  size="small"
                  style={{ marginLeft: "10px" }}
                >
                  赞美
                </Button>
              </span>
            </Hammer>
          </p>
          <p>
            <Hammer
              onTap={() => {
                this.routers("/Main/Lvdong");
              }}
            >
              <span>
                <Button
                  type="primary"
                  size="small"
                  style={{ marginLeft: "10px" }}
                >
                  律动
                </Button>
              </span>
            </Hammer>
          </p>
        </Drawer>
      </div>
    );
  }

  
  componentDidMount() {
    window.addEventListener("resize", this.handleResize.bind(this)); //监听窗口大小改变
    let router = this.props.location.pathname;

    console.log(router);
    this.setState({
      autoRouter: [router]
    });
  }
}

export default Main;



