import React, { Component } from 'react';

import { HashRouter ,Link,Route,Redirect} from 'react-router-dom';
import './App.css';
import Login from './Login/Login';
import Main from './Mian/Main';
class App extends Component {
  
  constructor(props) {
    super(props);
    this.state={

    }
}
  render() {
    return (
  
      <HashRouter>
      <div className="App">
          {/* <Link to="/Login" replace>one</Link>&nbsp;&nbsp;
          <Link to="/Main" replace>two</Link>&nbsp;&nbsp; */}
          <Route exact path="/" render={()=><Redirect to="/Main" />}></Route>
          <Route path="/Login" component={Login}></Route>
          <Route path="/Main" component={Main}></Route> 
      </div>
   </HashRouter> 
    );
  }

 
}

export default App;



