import React, { Component } from "react";
import { Menu, Icon, Button, Slider, Switch,Row,Col } from "antd";
import Pcs from "./pcmusicList/pclistMusic";
import zanmeiScss from "./zanmei.scss";
import data from "../../JSON/index.json";
import Hammer from "react-hammerjs";
import data2000 from "../../JSON/2000.json";
import "./zanmei.css";
// var Hammer = require('react-hammerjs');

const ButtonGroup = Button.Group;
console.log(Hammer);
class Zanmei extends Component {
  handleClick = e => {};
  constructor() {
    super();
    this.state = {
      datalist: data,
      audioURL:
        "https://github.com/sunzhongt/imgData/blob/master/music/%E6%80%9D%E6%85%95%E6%9B%B2.mp3?raw=true",
      audioSrc:
        "https://cdnimg103.lizhi.fm/audio_cover/2015/06/28/21069105442285575_320x320.jpg",
      audioTitle: "思慕曲",
      audioSumDate: "00:00",
      sumfileDate: "00:00",
      value: 0,
      isloop: true,
      max: 1,
      ispc: true,
      isIphone: false,
      islode:false
    };
  }

  ItmeSelecct = obj => {
    if (obj.key == "1") {
      this.setState({
        datalist: data
      });
    } else if (obj.key == "2") {
      this.setState({
        datalist: data2000
      });
    }
  };
  onTapPlay = e => {
    // console.log(this.refs.audio);
    this.refs.audio.play();
  };
  onTapPause = e => {
    // console.log(this.refs.audio);
    this.refs.audio.pause();
  };

  changeMusic = obj => {
    this.setState(
      {
        audioTitle: obj.musicTitle,
        audioSrc: obj.musicPIC,
        audioURL: obj.musicURL
      },
      () => {
        this.refs.audio.play();
      }
    );
  };

  // 总时间
  getSumDate() {

    if(this.refs.audio){
      var this_ = this;
      this.refs.audio.oncanplay = () => {
        // console.log($audio.duration)
        var s = Math.ceil(this.refs.audio.duration % 60); //35
        var min = parseInt(this.refs.audio.duration / 60); //1
        var h = parseInt(this.refs.audio.duration / 3600);
  
        h = h >= 10 ? h : "0" + h;
        min = min >= 10 ? min : "0" + min;
        s = s >= 10 ? s : "0" + s;
        this_.setState({
          sumfileDate: min + ":" + s
        });
  
        //  console.log(this.state.sumfileDate)
        this.setState({
          islode:true
        })
      };
    }else {
      return false
    }
  }

  //音频总时间
  audioSumTime() {
    if(this.refs.audio){
   var this_ = this;
    this.refs.audio.ontimeupdate = () => {
      var s = Math.ceil(this.refs.audio.currentTime % 60);
      var min = parseInt(this.refs.audio.currentTime / 60);
      var h = parseInt(this.refs.audio.currentTime / 3600);

      h = h >= 10 ? h : "0" + h;
      min = min >= 10 ? min : "0" + min;
      s = s >= 10 ? s : "0" + s;

      this_.setState(
        {
          audioSumDate: min + ":" + s,
          value:
            (this.refs.audio.currentTime / this.refs.audio.duration) *
            this.refs.audio.duration,
          max: Math.ceil(this.refs.audio.duration)
        },
        () => {
          //  console.log(this.state.value,this.state.max)
        }
      );
    };
    }else {
      return false
    }
 
  }
  componentWillMount() {
    var e = window.innerWidth;
    if (e <= 420) {
      this.setState({
        isIphone: true,
        ispc: false
      });
    } else if (e <= 1150) {
      this.setState({
        ispc: false,
        isIphone: false
      });
    } else {
      this.setState({
        ispc: true
      });
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize.bind(this));
    this.setState({
      islode:false
    })
    this.getSumDate = null;
    this.audioSumTime = null;
    this.setState({
      value: 0,
      isloop: true,
      max: 1,
      ispc: true,
      isIphone: false
    });
  }
  componentDidMount() {
    var this_ = this;
    window.addEventListener("resize", this.handleResize.bind(this)); //监听窗口大小改变

    this_.getSumDate();
    this_.audioSumTime();
  }

  handleResize = e => {
    // this.setState({
    //   innerWidth:e.target.innerWidth
    // })
    if (e.target.innerWidth <= 420) {
      this.setState({
        isIphone: true,
        ispc: false
      });
    } else if (e.target.innerWidth <= 1150) {
      this.setState({
        ispc: false,
        isIphone: false
      });
    } else {
      this.setState({
        ispc: true,
        isIphone: false
      });
    }
    console.log("浏览器窗口大小改变事件", e.target.innerWidth);
  };

  RonAfterChange(value) {}
  onChange(value) {
    this.refs.audio.currentTime =
      (value / Math.ceil(this.refs.audio.duration)) * this.refs.audio.duration;
  }
  isfun = () => {
    this.setState({
      isloop: !this.state.isloop
    });
  };
  render() {
    return (
      <div
        className={zanmeiScss.zanmei + " " + "zanmeicss"}
        style={{ paddingLeft: this.state.ispc ? "200px" : "0px" }}
      >
        <div className={zanmeiScss.zanmeiMenu}>
          <Menu
            onClick={this.handleClick}
            style={{ width: 198, display: this.state.ispc ? "block" : "none" }}
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            mode="inline"
            onSelect={({ item, key, selectedKeys }) => {
              this.ItmeSelecct({ item, key, selectedKeys });
            }}
          >
            <Menu.Item key="1">
              <Icon type="customer-service" theme="twoTone" />
              福音圣歌 0001-1000
            </Menu.Item>
            <Menu.Item key="2">
              <Icon type="customer-service" theme="twoTone" />
              福音圣歌 1001-2000
            </Menu.Item>
            <Menu.Item key="3">
              <Icon type="customer-service" theme="twoTone" />
              福音圣歌 2001-3000
            </Menu.Item>
            <Menu.Item key="4">
              <Icon type="customer-service" theme="twoTone" />
              福音圣歌 3001-4000
            </Menu.Item>
            <Menu.Item key="5">
              <Icon type="customer-service" theme="twoTone" />
              福音圣歌 4001-5000
            </Menu.Item>
          </Menu>
        </div>

        <div className={zanmeiScss.zanmeiContent}>
          {this.state.datalist.map(ele => {
            return (
              <Pcs
                changeMusic={this.changeMusic}
                musicPIC={ele.musicPIC}
                musicURL={ele.musicURL}
                tagsName={ele.tagsName}
                id={ele.id}
                musicTitle={ele.musicTitle}
                musicAuthor={ele.musicAuthor}
                key={ele.id}
              />
            );
          })}
        </div>

        <div
          className={zanmeiScss.Content}
          style={{ display: this.state.isIphone ? "none" : "inline-block" }}
        >
          <span
            style={{ display: this.state.isIphone ? "none" : "inline-block" }}
          >
            <Hammer onTap={this.onTapPlay}>
              <span>
                <Button
                  type="primary"
                  icon="play-circle"
                  size="large"
                  shape="circle"
                />
              </span>
            </Hammer>

            <Hammer onTap={this.onTapPause}>
              <span>
                <Button
                  type="primary"
                  icon="pause-circle"
                  size="large"
                  shape="circle"
                />
              </span>
            </Hammer>
          </span>

          <img src={this.state.audioSrc} alt="" />

          <span className="tiles"> 《{this.state.audioTitle}》 </span>
          <div
            className="huadong"
            style={{ display: this.state.isIphone ? "none" : "inline-block" }}
          >
            <Slider
              min={1}
              max={this.state.max}
              value={this.state.value}
              onChange={value => {
                return this.onChange(value);
              }}
            />
          </div>

          <div className={zanmeiScss.date}>
            {this.state.audioSumDate}/{this.state.sumfileDate}
          </div>
          <div className={zanmeiScss.Switchs}>
            <Switch
              defaultChecked
              onChange={this.isfun}
              checkedChildren={<Icon type="retweet" />}
              unCheckedChildren={<Icon type="close" />}
            />
          </div>
        </div>
        {/* xddddddddddddddddddd */}
        <div
          className={zanmeiScss.ContentIphone}
          style={{ display: !this.state.isIphone ? "none" : "inline-block" }}
        >
          {/* <img
            src={this.state.audioSrc}
            alt=""
            style={{ width: "40px", height: "40px", borderRadius: "50%" }}
          />
        
      
         
          <Hammer onTap={this.onTapPlay}>
            <span className={zanmeiScss.play}>
              <Icon
                type="play-circle"
                style={{ color: "#fff", fontSize: "25px" }}
              />
            </span>
          </Hammer>

          <Hammer onTap={this.onTapPause}>
            <span className={zanmeiScss.Pause}>
              <Icon
                type="pause-circle"
                style={{ color: "#fff", fontSize: "25px" }}
              />
            </span>
          </Hammer>
          <div className={zanmeiScss.nameTitle}>{this.state.audioTitle}</div>

          <div className={zanmeiScss.iphoneDate}>
            {this.state.audioSumDate}/{this.state.sumfileDate}
          </div> */}
          <Row style={{height:"100%"}}>
            <Col span={5}  style={{height:"100%"}}>
             <img
            src={this.state.audioSrc}
            alt=""
            style={{ width: "40px", height: "40px", borderRadius: "50%" }}
          />
            </Col>
            <Col span={19}  style={{height:"100%"}}>
             <Row style={{height:"100%"}}>

             <Col span={24} className={zanmeiScss.top}  >
             <Hammer onTap={this.onTapPlay}>
            <span className={zanmeiScss.play}>
              <Icon
                type="play-circle"
                style={{ color: "#fff", fontSize: "25px" }}
              />
            </span>
          </Hammer>

          <Hammer onTap={this.onTapPause}>
            <span className={zanmeiScss.Pause}>
              <Icon
                type="pause-circle"
                style={{ color: "#fff", fontSize: "25px" }}
              />
            </span>
          </Hammer>
          <div className={zanmeiScss.nameTitle}>{this.state.audioTitle}</div>

          <div className={zanmeiScss.iphoneDate}>
            {this.state.audioSumDate}/{this.state.sumfileDate}
          </div>
             </Col>           
             <Col span={24} className={zanmeiScss.bottom}>
             <Slider
              min={1}
              max={this.state.max}
              value={this.state.value}
              onChange={value => {
                return this.onChange(value);
              }}
            />
             </Col>           
          </Row>
            </Col>
          </Row>
         
         
        
      
       
       
         
        </div>
        <audio
          src={this.state.audioURL}
          id="audio"
          ref="audio"
          loop={this.state.isloop}
        />
      </div>
    );
  }
}

export default Zanmei;



